// src/pages/hr/openings.js
import { useState, useEffect } from "react";
import HRSidebar from "@/layouts/hrSidebar";
import HRHeader from "@/layouts/hrHeader";
import { Toaster, toast } from "react-hot-toast";
import { Icon } from "@iconify/react";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import { supabase } from "lib/supabase";
import { uploadFileToDrive } from "utils/driveUtils";
import JobDescriptionModal from "@/components/JobDescriptionModal";

export default function Openings({ mode = "light", toggleMode }) {
    const [isSidebarOpen, setIsSidebarOpen] = useState(false);
    const [openings, setOpenings] = useState([]);
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [file, setFile] = useState(null);
    const [expiresOn, setExpiresOn] = useState("");
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedOpening, setSelectedOpening] = useState(null);

    useEffect(() => {
        fetchOpenings();
    }, []);

    const fetchOpenings = async () => {
        const { data, error } = await supabase
            .from("job_openings")
            .select("*")
            .order("created_at", { ascending: false });
        if (error) {
            toast.error("Failed to load job openings.");
            console.error("Fetch error:", error);
        } else {
            setOpenings(data);
        }
    };

    const handleSubmitJob = async (e) => {
        e.preventDefault();
        let fileUrl = null;
        let fileId = null;

        if (file) {
            const fileData = await fileToBase64(file);
            const fileType = file.name.split(".").pop().toLowerCase() === "pdf" ? "pdf" : "docx";
            const uploadResult = await uploadFileToDrive(
                "job-opening-" + Date.now(),
                fileData.split(",")[1], // Remove "data:..." prefix
                fileType,
                null
            );
            fileUrl = uploadResult.url;
            fileId = uploadResult.fileId;
            if (!fileUrl) {
                toast.error("Failed to upload file.");
                return;
            }
        }

        const jobData = {
            title,
            description: description || null,
            file_url: fileUrl,
            file_id: fileId,
            expires_on: expiresOn,
            is_expired: false,
        };

        const { error } = await supabase.from("job_openings").insert([jobData]);
        if (error) {
            toast.error("Failed to submit job opening.");
            console.error("Insert error:", error);
        } else {
            toast.success("Job opening added successfully!", { icon: "✅" });
            setTitle("");
            setDescription("");
            setFile(null);
            setExpiresOn("");
            fetchOpenings();
        }
    };

    const fileToBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result);
            reader.onerror = (error) => reject(error);
        });
    };

    const handleFileChange = (e) => setFile(e.target.files[0]);

    const handleOpenModal = (opening) => {
        setSelectedOpening(opening.title);
        setIsModalOpen(true);
    };

    const handleCloseModal = () => {
        setIsModalOpen(false);
        setSelectedOpening(null);
    };

    const handleProceed = () => {
        // Placeholder for applicant action (e.g., redirect to interview form)
        console.log("Proceed clicked for:", selectedOpening);
        handleCloseModal();
    };

    return (
        <DndProvider backend={HTML5Backend}>
            <div
                className={`min-h-screen flex flex-col ${
                    mode === "dark" ? "bg-gradient-to-b from-gray-900 to-gray-800" : "bg-gradient-to-b from-gray-50 to-gray-100"
                }`}
            >
                <Toaster position="top-right" reverseOrder={false} />
                <HRHeader mode={mode} toggleMode={toggleMode} onLogout={() => localStorage.removeItem("hr_session")} />
                <div className="flex flex-1">
                    <HRSidebar
                        isOpen={isSidebarOpen}
                        mode={mode}
                        onLogout={() => localStorage.removeItem("hr_session")}
                        toggleSidebar={() => setIsSidebarOpen(!isSidebarOpen)}
                    />
                    <div
                        className={`flex-1 p-6 transition-all duration-300 ${
                            isSidebarOpen ? "md:ml-[300px]" : "md:ml-[80px]"
                        }`}
                    >
                        <div className="max-w-5xl mx-auto">
                            <div className="flex justify-between items-center mb-6">
                                <div className="flex items-center gap-2">
                                    <Icon icon="mdi:briefcase" className="w-6 h-6 text-[#f05d23]" />
                                    <h3
                                        className={`text-xl font-bold ${
                                            mode === "dark" ? "text-white" : "text-[#231812]"
                                        }`}
                                    >
                                        Job Openings
                                    </h3>
                                </div>
                            </div>

                            {/* Job Form */}
                            <div
                                className={`p-6 rounded-lg shadow-lg mb-8 border-t-4 border-[#f05d23] ${
                                    mode === "dark" ? "bg-gray-800" : "bg-white"
                                }`}
                            >
                                <div className="flex items-center mb-4">
                                    <Icon icon="mdi:briefcase-plus" className="w-6 h-6 text-[#f05d23] mr-2" />
                                    <h3
                                        className={`text-xl font-bold ${
                                            mode === "dark" ? "text-white" : "text-[#231812]"
                                        }`}
                                    >
                                        Add New Job Opening
                                    </h3>
                                </div>
                                <form onSubmit={handleSubmitJob} className="space-y-6">
                                    <div>
                                        <label
                                            className={`block text-sm font-medium mb-2 ${
                                                mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                            }`}
                                        >
                                            Job Title <span className="text-red-500">*</span>
                                        </label>
                                        <div className="relative">
                                            <Icon
                                                icon="mdi:format-title"
                                                className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]"
                                            />
                                            <input
                                                type="text"
                                                value={title}
                                                onChange={(e) => setTitle(e.target.value)}
                                                className={`w-full pl-10 pr-4 py-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] ${
                                                    mode === "dark"
                                                        ? "bg-gray-700 border-gray-600 text-white"
                                                        : "bg-gray-50 border-gray-300 text-[#231812]"
                                                }`}
                                                placeholder="e.g., Software Engineer"
                                                required
                                            />
                                        </div>
                                    </div>
                                    <div>
                                        <label
                                            className={`block text-sm font-medium mb-2 ${
                                                mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                            }`}
                                        >
                                            Description (Optional)
                                        </label>
                                        <textarea
                                            value={description}
                                            onChange={(e) => setDescription(e.target.value)}
                                            className={`w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] min-h-[100px] ${
                                                mode === "dark"
                                                    ? "bg-gray-700 border-gray-600 text-white"
                                                    : "bg-gray-50 border-gray-300 text-[#231812]"
                                            }`}
                                            placeholder="Enter job description..."
                                        />
                                    </div>
                                    <div>
                                        <label
                                            className={`block text-sm font-medium mb-2 ${
                                                mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                            }`}
                                        >
                                            Upload Job Description (DOCX/PDF, Optional)
                                        </label>
                                        <div className="relative">
                                            <Icon
                                                icon="mdi:file-upload"
                                                className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]"
                                            />
                                            <input
                                                type="file"
                                                accept=".pdf,.docx"
                                                onChange={handleFileChange}
                                                className={`w-full pl-10 pr-4 py-3 border rounded-lg ${
                                                    mode === "dark"
                                                        ? "bg-gray-700 border-gray-600 text-gray-300"
                                                        : "bg-gray-50 border-gray-300 text-[#231812]"
                                                }`}
                                            />
                                        </div>
                                    </div>
                                    <div>
                                        <label
                                            className={`block text-sm font-medium mb-2 ${
                                                mode === "dark" ? "text-gray-300" : "text-[#231812]"
                                            }`}
                                        >
                                            Expires On <span className="text-red-500">*</span>
                                        </label>
                                        <div className="relative">
                                            <Icon
                                                icon="mdi:calendar"
                                                className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]"
                                            />
                                            <input
                                                type="date"
                                                value={expiresOn}
                                                onChange={(e) => setExpiresOn(e.target.value)}
                                                className={`w-full pl-10 pr-4 py-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] ${
                                                    mode === "dark"
                                                        ? "bg-gray-700 border-gray-600 text-white"
                                                        : "bg-gray-50 border-gray-300 text-[#231812]"
                                                }`}
                                                required
                                            />
                                        </div>
                                    </div>
                                    <button
                                        type="submit"
                                        className="w-full py-3 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 shadow-md flex items-center justify-center gap-2"
                                    >
                                        <Icon icon="mdi:plus" width={20} height={20} />
                                        Add Job Opening
                                    </button>
                                </form>
                            </div>

                            {/* Job Openings Table */}
                            <div
                                className={`rounded-lg shadow-lg overflow-hidden ${
                                    mode === "dark" ? "bg-gray-800" : "bg-white"
                                }`}
                            >
                                <div className="max-h-[500px] overflow-y-auto">
                                    <table className="w-full">
                                        <thead className="sticky top-0 z-10">
                                        <tr className={`${mode === "dark" ? "bg-gray-700" : "bg-gray-100"}`}>
                                            <th className="p-4 text-left text-sm font-semibold">Title</th>
                                            <th className="p-4 text-left text-sm font-semibold">Expires On</th>
                                            <th className="p-4 text-left text-sm font-semibold">Status</th>
                                            <th className="p-4 text-left text-sm font-semibold">Actions</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {openings.map((opening) => (
                                            <tr
                                                key={opening.id}
                                                className={`border-b hover:bg-opacity-80 transition duration-200 ${
                                                    mode === "dark"
                                                        ? "border-gray-700 hover:bg-gray-700"
                                                        : "border-gray-200 hover:bg-gray-50"
                                                }`}
                                            >
                                                <td className="p-4 text-sm">{opening.title}</td>
                                                <td className="p-4 text-sm">
                                                    {new Date(opening.expires_on).toLocaleDateString()}
                                                </td>
                                                <td className="p-4 text-sm">
                                                    {new Date(opening.expires_on) > new Date() ? "Active" : "Expired"}
                                                </td>
                                                <td className="p-4 text-sm">
                                                    <button
                                                        onClick={() => handleOpenModal(opening)}
                                                        className="px-3 py-1 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 flex items-center gap-2"
                                                    >
                                                        <Icon icon="mdi:eye" width={16} height={16} />
                                                        View
                                                    </button>
                                                </td>
                                            </tr>
                                        ))}
                                        </tbody>
                                    </table>
                                    {openings.length === 0 && (
                                        <p
                                            className={`text-center p-4 italic ${
                                                mode === "dark" ? "text-gray-400" : "text-gray-500"
                                            }`}
                                        >
                                            No job openings available.
                                        </p>
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <JobDescriptionModal
                    isOpen={isModalOpen}
                    onClose={handleCloseModal}
                    onProceed={handleProceed}
                    selectedOpening={selectedOpening}
                />
                <footer
                    className={`p-4 text-center text-sm shadow-inner ${
                        mode === "dark" ? "bg-gray-900 text-gray-400" : "bg-white text-gray-500"
                    }`}
                >
                    © {new Date().getFullYear()} Growthpad Consulting Group. All rights reserved.
                </footer>
            </div>
        </DndProvider>
    );
}