// src/components/JobDescriptionModal.js
import { useState, useEffect } from "react";
import { Icon } from "@iconify/react";
import { supabase } from "lib/supabase";
import { Document, Page, pdfjs } from "react-pdf";
import "react-pdf/dist/esm/Page/AnnotationLayer.css";
import "react-pdf/dist/esm/Page/TextLayer.css";

// Set worker for react-pdf
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

export default function JobDescriptionModal({ isOpen, onClose, onProceed, selectedOpening }) {
    const [jobDetails, setJobDetails] = useState(null);
    const [numPages, setNumPages] = useState(null);
    const [pageNumber, setPageNumber] = useState(1);

    useEffect(() => {
        if (selectedOpening) {
            const fetchJobDetails = async () => {
                const { data, error } = await supabase
                    .from("job_openings")
                    .select("title, description, file_url, expires_on")
                    .eq("title", selectedOpening)
                    .single();
                if (error) console.error("Error fetching job details:", error);
                else setJobDetails(data);
            };
            fetchJobDetails();
        }
    }, [selectedOpening]);

    const onDocumentLoadSuccess = ({ numPages }) => {
        setNumPages(numPages);
    };

    if (!isOpen || !selectedOpening || !jobDetails) return null;

    const { title, description, file_url, expires_on } = jobDetails;
    const isPdf = file_url && file_url.endsWith(".pdf");
    const isExpired = new Date(expires_on) < new Date();

    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
            <div className="bg-white rounded-lg p-6 max-w-2xl w-full mx-4 shadow-xl animate-fade-in max-h-[90vh] overflow-y-auto">
                <div className="flex items-center mb-4">
                    <Icon icon="mdi:briefcase" className="w-6 h-6 text-[#f05d23] mr-2" />
                    <h2 className="text-2xl font-bold text-[#231812]">{title}</h2>
                </div>
                <div className="mb-4">
                    <p className="text-[#231812]">
                        <strong>Status:</strong> {isExpired ? "Expired" : "Active"}
                    </p>
                    <p className="text-[#231812]">
                        <strong>Expires On:</strong> {new Date(expires_on).toLocaleDateString()}
                    </p>
                </div>
                {description && (
                    <div className="mb-4">
                        <strong className="text-[#231812]">Description:</strong>
                        <p className="text-[#231812]" dangerouslySetInnerHTML={{ __html: description }} />
                    </div>
                )}
                {file_url && (
                    <div className="mb-4">
                        <strong className="text-[#231812]">Job Description:</strong>
                        {isPdf ? (
                            <div>
                                <Document file={file_url} onLoadSuccess={onDocumentLoadSuccess}>
                                    <Page pageNumber={pageNumber} width={400} />
                                </Document>
                                <div className="flex justify-between mt-2">
                                    <button
                                        onClick={() => setPageNumber((prev) => Math.max(prev - 1, 1))}
                                        disabled={pageNumber <= 1}
                                        className="px-2 py-1 bg-gray-300 rounded hover:bg-gray-400"
                                    >
                                        Previous
                                    </button>
                                    <p>
                                        Page {pageNumber} of {numPages}
                                    </p>
                                    <button
                                        onClick={() => setPageNumber((prev) => Math.min(prev + 1, numPages))}
                                        disabled={pageNumber >= numPages}
                                        className="px-2 py-1 bg-gray-300 rounded hover:bg-gray-400"
                                    >
                                        Next
                                    </button>
                                </div>
                            </div>
                        ) : (
                            <p>
                                <a
                                    href={file_url}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="text-[#f05d23] underline"
                                >
                                    View Job Description (DOCX)
                                </a>
                            </p>
                        )}
                    </div>
                )}
                <div className="flex justify-end gap-4">
                    <button
                        onClick={onClose}
                        className="px-4 py-2 bg-gray-300 text-[#231812] rounded-lg hover:bg-gray-400 transition duration-200"
                    >
                        Cancel
                    </button>
                    <button
                        onClick={onProceed}
                        className="px-4 py-2 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 flex items-center"
                    >
                        Proceed
                        <Icon icon="mdi:arrow-right" width={20} height={20} className="ml-2" />
                    </button>
                </div>
            </div>
        </div>
    );
}